<template>
  <div>
    <form id="form" v-on:submit.prevent="login">
      <div class="input-field">
        <label>&nbsp;&nbsp;&nbsp;&nbsp;</label>
        <input type="number"  placeholder="student number" v-model="stuNum">
      </div>
      <div class="input-field">
        <label>&nbsp;&nbsp;&nbsp;&nbsp;</label>
        <input type="password"  placeholder="password" v-model="password">
      </div>
      <input type="submit" value="" >
      <div class="login-choose">
        <a href=""></a>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <a href=""></a>
      </div>
    </form> 
  </div>
</template>

<script>
import code from './code.js'
import Cookies from 'js.cookie'
// import router from '../../router'

let vm = {
  name: 'loginForm',
  computed: {
    validation: function () {
      return {
        name: !!this.stuNum.trim(),
        password: !!this.password
      }
    },
    isValid: function () {
      let validation = this.validation
      return Object.keys(validation).every(function (key) {
        return validation[key]
      })
    },
    isShow () {
      return this.$store.state.mask.maskIsShow
    },
    stuNum: {
      get () {
        return this.$store.state.autoInfo.stuNum
      },
      set (value) {
        this.$store.commit('updateStuNum', '' + value)
      }
    },
    password: {
      get () {
        return this.$store.state.autoInfo.password
      },
      set (value) {
        this.$store.commit('updatePassword', value)
      }
    }
  },
  methods: {
    toggleMask: function () {
      this.$store.commit('toggleMaskState')
    },
    login: function () {
      if (this.isValid) {
        this.toggleMask()
        this.$store.state.mask.maskIsShow && code((res) => {
          // console.log(data)
          if (res.data.status === 'success') {
            this.$store.commit('login', res.data.name)
            Cookies.set('sessionId', res.data.info)
            Cookies.set('name', res.data.name)
            window.alert('')
            this.$router.push('/')
          } else {
            window.alert('')
          }
        })
      } else {
        window.alert('')
      }
    }
  }
}
export default vm
</script>
<style lang="scss" scoped>
    @import "../../assets/sass_tool/base.scss";
    @import "../../assets/sass_tool/_size.scss";
    @import "../../assets/sass_tool/_center.scss";
   
    form{
      position: relative;
      width: 90%;
      margin: 36% auto;
      .input-field{
        margin: 30px 0;
        overflow: hidden;
        display: flex;
        label{
          font-size: 18px;
          line-height: 30px;
          flex: 0 1 auto;
          white-space:nowrap;
          width: 72px;
        }
        input{
          flex: 1;
          @include size(75%,30px);
          border: solid 1px #1989fa;
          border-radius: 3px;
          padding-left: 5px;
          font-size: 18px;
        }
      }
      input[type="submit"]{
        @include size(100%,40px);
        background: #1989fa;
        line-height: 40px;
        color: #fff;
        font-size: 20px;
        border: none;
        margin:15px 0;
      }
      .login-choose{
        text-align: center;
        a{
          color: #1989fa;
        }
      }
    }
    @media  (min-width: 450px){
      form{
          @include center();
          margin: 0 !important;
      }
    }
</style>


