<template>
  <div>
    <form id="form" v-on:submit.prevent="regist">
      <div class="input-field">
        <label>&nbsp;&nbsp;&nbsp;&nbsp;</label>
        <input type="number"  placeholder="" v-model="stuNum">
      </div>
      <div class="input-field">
        <label>&nbsp;&nbsp;&nbsp;&nbsp;</label>
        <input type="password"  placeholder="" v-model="password">
      </div>
      <div class="input-field">
        <label></label>
        <input type="password"  placeholder="" v-model="comfirePassword">
      </div>
      <div class="input-field">
        <label id="idcard"></label>
        <input type="number"  placeholder="" v-model="idCard">
      </div>
      <input type="submit" value="" >
      <div class="login-choose">
        <router-link to="/login"><a href=""></a></router-link>
      </div>
    </form> 
  </div>
</template>

<script>
import post from './post.js'
export default {
  name: 'regist',
  data () {
    return {
      comfirePassword: ''
    }
  },
  computed: {
    validation: function () {
      let passwordRE = /[a-zA-Z]/
      return {
        password: passwordRE.test(this.password) && this.password.length >= 6 && this.password.length < 20,
        comfirePassword: this.comfirePassword === this.password,
        stuNum: !!this.stuNum.trim(),
        idCard: this.idCard.length === 6
      }
    },
    password: {
      get () {
        return this.$store.state.register.password
      },
      set (value) {
        this.$store.commit('setPassword', value)
      }
    },
    stuNum: {
      get () {
        return this.$store.state.register.stuNum
      },
      set (value) {
        this.$store.commit('setStuNum', '' + value)
      }
    },
    idCard: {
      get () {
        return this.$store.state.register.idCard
      },
      set (value) {
        this.$store.commit('setIdCard', '' + value)
      }
    }
  },
  methods: {
    regist () {
      // console.log(this.isValid)
      if (this.isValid()) {
        post().then((res) => {
          return res.data
        }).then((data) => {
          if (data.status === 1) {
            window.alert('')
            this.$router.push('/login')
          } else {
            window.alert(data.msg)
          }
        })
      }
    },
    isValid: function () {
      let errorInfo = {
        name: '',
        password: '20',
        comfirePassword: '',
        stuNum: '',
        idCard: ''
      }
      let validation = this.validation
      return Object.keys(validation).every(function (key) {
        validation[key] || window.alert(errorInfo[key])
        return validation[key]
      })
    }
  }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/sass_tool/base.scss";
    @import "../../assets/sass_tool/_size.scss";
    @import "../../assets/sass_tool/_center.scss";
   
    form{
      position: relative;
      width: 90%;
      margin: 30% auto;
      .input-field{
        margin: 30px 0;
        overflow: hidden;
        display: flex;
        label{
          font-size: 18px;
          line-height: 30px;
          flex: 0 1 auto;
          white-space:nowrap;
          width: 90px;
        }
        input{
          flex: 1;
          @include size(75%,30px);
          border: solid 1px #1989fa;
          border-radius: 3px;
          padding-left: 5px;
          font-size: 18px;
        }
      }
      input[type="submit"]{
        @include size(100%,40px);
        background: #1989fa;
        line-height: 40px;
        color: #fff;
        font-size: 20px;
        border: none;
        margin:15px 0;
      }
      .login-choose{
        text-align: center;
        a{
          color: #1989fa;
        }
      }
    }

    #idcard{
      width: 150px;
    }

    @media  (min-width: 400px){
      form{
          @include center();
          margin: 0 !important;
      }
    }
</style>


